<template>
  <el-container style="height: 100%;font-family:'Helvetica Neue',Helvetica,'PingFang SC','Hiragino Sans GB','Microsoft YaHei','',Arial,sans-serif;font-size: 18px;"
                direction="vertical" >
      <el-container>
        <el-col :span="8">
          <el-row>
            <router-link :to="{name:'Buy'}"></router-link>
          </el-row>
          <el-row>
            <router-link :to="{name:'Repair'}"></router-link>
          </el-row>
          <el-row>
            <router-link :to="{name:'Health'}"></router-link>
          </el-row>
        </el-col>
        <el-col :span="8">

          <el-row>
            <router-link :to="{name:'Chat'}"></router-link>
          </el-row>
          <el-row >
            <router-link :to="{name:'HealthQRCode'}"></router-link>
          </el-row>
          <el-row >
            <router-link :to="{name:'ViewAdminPostList'}"></router-link>
          </el-row>
        </el-col>
        <el-col :span="8">
          <el-row>
            <router-link :to="{name:'Post'}"></router-link>
          </el-row>
          <el-row>
            <router-link :to="{name:'ViewPartyMember'}"></router-link>
          </el-row>
          <el-row>
            <router-link :to="{name:'Account'}"></router-link>
          </el-row>
        </el-col>
      </el-container>
  </el-container>
</template>
<script>
export default {
  name: "User",
  data() {
    return {
      isRouterAlive:true,
      id: ''
    }
  },
  methods: {
    flushRouter() {
      this.isRouterAlive = false
      this.$nextTick(function () {
        this.isRouterAlive = true
      })
    },
  },
  created() {
    this.id = window.sessionStorage.getItem('userid');
    console.log('user' + this.id)
  }
}
</script>
<style scoped lang="scss">
html,
body, #app,
.el-container {
  padding: 0px;
  margin: 0px;
  height: 100%;
}

.el-row {
  border: #ffffff;
}

.el-col {
  border: #ffffff;
}

.el-header,.el-footer {
  background-color: #3f5c6d2c;
  color: #333;
  text-align: center;
  line-height: 60px;
}


.el-main {
  background-color: transparent;
  color: #333;
  text-align: center;
  line-height: 160px;
}


a {
  text-decoration: none;

}

a:link {
  color: black
}

/*  */
a:visited {
  color: black
}

/*  */
a:hover {
  color: black
}

/*  */
a:active {
  color: black
}

/*  */
.router-link-active {
  text-decoration: none;
}
.el-row{
  border: 1px solid #AAAAAA;
}
</style>



